import {ILoginData} from "../components/Login";
import {AUTHENTICATION, SIGNUP} from "../constants/routes";
import {Simulate} from "react-dom/test-utils";
import error = Simulate.error;

export interface IAuthenticationData {
    firstName: string;
    lastName: string;
    email: string;
    password: string;
}

const requestOptions = (jsonData: string) => {
    return {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: jsonData,
    };
};

export const logIn = (data: ILoginData) => {
    console.log();
    fetch("http://localhost:8080/login", requestOptions(JSON.stringify(data)))
        .then((response) => console.log(response.json()))
        .catch((error) => console.log(error));
};

export const signUp = (data: IAuthenticationData) => {
    console.log("sing up: ", data);

    fetch("http://localhost:8080" + AUTHENTICATION + SIGNUP, requestOptions(JSON.stringify(data)))
        .then((response) => console.log("response: ", response)).catch((error) => console.log("error: " +
        error))
}
